html, body, #root {
  height: 100%;
}


/* RESET */
* {
  box-sizing: border-box;
}

/* BODY */
body {
  margin: 0;
  font-family: "Segoe UI", Arial, sans-serif;
  background: #eef5f4;
}

/* APP */
.app {
  height: 100vh;
  display: flex;
  flex-direction: column;
}

/* HEADER */
.header {
  background: #6fc7b5;
  color: white;
  padding: 16px;
  font-size: 22px;
  font-weight: 600;
  text-align: center;
}

/* CONTENT */
.content {
  flex: 1;
  padding: 0;
}

/* TAB BAR */
.tab-bar {
  display: flex;
  background: white;
  border-top: 1px solid #ddd;
}

.tab-bar button {
  flex: 1;
  padding: 12px;
  border: none;
  background: none;
  font-size: 14px;
  cursor: pointer;
}

.tab-bar .active {
  background: #6fc7b5;
  color: white;
  font-weight: 600;
}

/* HOME BACKGROUND */
.home {
  height: 100%;
  background-image: url("./assets/image.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
}

/* SOFT OVERLAY */
.home-overlay {
  height: 100%;
  background: rgba(230, 245, 242, 0.65);
  backdrop-filter: blur(4px);
  padding: 32px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

/* TITLE */
.home-title {
  margin-top: 10px;
  font-size: 36px;
  color: #2f5f59;
}

.home-subtitle {
  margin-bottom: 28px;
  color: #4a7f78;
  font-size: 16px;
}

/* BUTTON GRID */
.home-buttons {
  width: 100%;
  max-width: 420px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

/* IPAD */
@media (min-width: 768px) {
  .home-buttons {
    grid-template-columns: 1fr 1fr;
    max-width: 700px;
  }
}

@media (min-width: 768px) {
  .home-btn {
    padding: 26px 16px;
  }

  .home-task-icon {
    width: 120px;
    height: 120px;
  }

  .home-btn-label {
    font-size: 16px;
  }
}


/* BIG MODERN BUTTONS */
.home-btn {
  padding: 20px 12px;
  border-radius: 18px;
  border: none;
  background: linear-gradient(135deg, #ffffff, #e6f4f1);
  color: #2f5f59;
  box-shadow: 0 8px 18px rgba(0,0,0,0.12);
  cursor: pointer;
  transition: transform 0.15s ease, box-shadow 0.15s ease;

  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}



.home-btn:hover {
  transform: translateY(-3px);
  box-shadow: 0 12px 24px rgba(0,0,0,0.18);
}


/* BACK BUTTON */
.back-btn {
  background: transparent;
  border: none;
  font-size: 16px;
  font-weight: 600;
  color: #3a6f68;
  cursor: pointer;
  margin-bottom: 16px;
}

/* TITLES */
.tasks-title {
  text-align: center;
  font-size: 28px;
  margin-bottom: 6px;
}

.tasks-subtitle {
  text-align: center;
  color: #4f7f77;
  margin-bottom: 28px;
}

/* TASK BUBBLES */
.tasks-list {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.task-bubble {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 22px;
  padding: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-shadow:
    0 10px 25px rgba(0, 0, 0, 0.12),
    inset 0 1px 0 rgba(255,255,255,0.8);
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.task-bubble:hover {
  transform: translateY(-2px);
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.15);
}

.task-bubble.done {
  opacity: 0.75;
}

/* TASK TEXT */
.task-info h3 {
  margin: 0;
  font-size: 18px;
}

.task-time {
  font-size: 12px;
  color: #5e8c83;
}

/* BUTTONS */
.task-btn {
  border: none;
  padding: 10px 18px;
  border-radius: 22px;
  font-weight: 600;
  background: #6fc7b5;
  color: white;
  cursor: pointer;
}

.task-btn.undo {
  background: #aacfc6;
}

/* BACK TO HOME BUTTON */
.back-home-btn {
  background: white;
  border: none;
  padding: 10px 18px;
  border-radius: 22px;
  font-weight: 600;
  font-size: 14px;
  color: #3a6f68;
  cursor: pointer;
  margin-bottom: 18px;
  box-shadow: 0 6px 14px rgba(0, 0, 0, 0.15);
  transition: transform 0.15s ease, box-shadow 0.15s ease;
}

.back-home-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.18);
}



/* IMAGE */
.home-task-icon {
  width: 110px;
  height: 110px;
  object-fit: contain;
}

/* TEXT ON TOP OF IMAGE */
.home-btn-label {
  font-size: 14px;
  font-weight: 700;
  color: #2f5f59;
  text-align: center;
}

/* TASKS BACKGROUND */
.tasks-page {
  min-height: 100vh;
  background-image: url("./tabs/assets/tasks-bg.jpg");
  background-size: cover;
  background-position: center;
  position: relative;
}

/* TASKS OVERLAY (soft + clean) */
.tasks-overlay {
  height: 100%;
  background: rgba(230, 245, 242, 0.65);
  backdrop-filter: blur(4px);
  padding: 32px 20px;
  display: flex;
  flex-direction: column;
}

.shop-btn {
  background: linear-gradient(135deg, #ffffff, #e8fff7);
}

.shop-btn .home-btn-label {
  font-size: 16px;
}


/* SHOP SECTION */
.shop-section-title {
  margin-top: 30px;
  margin-bottom: 16px;
  font-size: 22px;
  color: #2f5f59;
}

/* SHOP GRID */
.shop-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 16px;
}

/* iPad and up */
@media (min-width: 768px) {
  .shop-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

/* SHOP CARD */
.shop-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 22px;
  padding: 20px 14px;
  text-align: center;
  box-shadow:
    0 10px 25px rgba(0, 0, 0, 0.12),
    inset 0 1px 0 rgba(255,255,255,0.8);
}

/* ICON */
.shop-icon {
  font-size: 42px;
  margin-bottom: 10px;
}

/* NAME */
.shop-name {
  margin: 6px 0;
  font-size: 16px;
  font-weight: 600;
}

/* PRICE */
.shop-price {
  margin: 6px 0 12px;
  font-size: 14px;
  color: #4f7f77;
}

/* BUY BUTTON */
.shop-buy-btn {
  border: none;
  padding: 10px 18px;
  border-radius: 22px;
  font-weight: 700;
  background: #6fc7b5;
  color: white;
  cursor: pointer;
}

.shop-buy-btn:active {
  transform: scale(0.96);
}

/* SHOP IMAGE */
.shop-image {
  width: 100%;
  height: 120px;
  object-fit: contain;
  margin-bottom: 12px;
}

/* iPad and up */
@media (min-width: 768px) {
  .shop-image {
    height: 140px;
  }
}

/* REWARDS CARDS */
.rewards-cards {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20px;
  margin-top: 30px;
}

.reward-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: 22px;
  padding: 28px 20px;
  text-align: center;
  box-shadow:
    0 10px 25px rgba(0, 0, 0, 0.12),
    inset 0 1px 0 rgba(255,255,255,0.8);
}

.reward-card h3 {
  font-size: 36px;
  margin: 10px 0 4px;
}

.reward-card p {
  margin: 0;
  font-size: 14px;
  color: #4f7f77;
  font-weight: 600;
}

.reward-icon {
  font-size: 36px;
}

/* SmugPoints styling */
.reward-card.smug {
  background: linear-gradient(135deg, #ffffff, #eef6ff);
}

.reward-card.smug p {
  color: #3b82f6;
}




